    background-color: #06080F;
    color: #fff;
}
body{
    height: 100vh;
    width: 100vw;
    overflow-x: hidden;
    overflow-y: hidden;
}
input,p,span{
    font-family: 'Cormorant Garamond', serif;
    font-size: 13px;
    letter-spacing: .5px;
    background-color: transparent;

}
div{
    background-color: transparent;
}
button{
    cursor: pointer;
}
